@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700;900&display=swap);
.container {
    max-width: 1440px;
    width: 100%;
    margin: 0px auto;
    padding: 40px 20px 20px;
}

@media (min-width: 768px) {
    .container {
        padding: 40px 20px;
    }
}

.header {
    border-bottom: 2px solid #000000;
}

.header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 20px;
}

.header__logotype {
    width: 100px;
    height: 80px;
}

.header__logotype img {
    width: 100%;
    height: 100%;
}

.header__action {
    display: flex;
    align-items: flex-end;
}

.header__action-register {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-right: 20px;
    font-size: 14px;
    font-weight: 400;
    color: #000000;
}

@media (min-width: 460px) {
    .header__action-register {
        font-size: 16px;
    }
}

.header__action-register img {
    width: 20px;
    height: 25px;
    margin-right: 10px;
}

.header__action-login {
    font-size: 14px;
    font-weight: 400;
    color: #000000;
}

@media (min-width: 460px) {
    .header__action-login {
        font-size: 16px;
    }
}

.header__action-profile {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-right: 20px;
    font-size: 14px;
    font-weight: 400;
    color: #000000;
}

@media (min-width: 460px) {
    .header__action-profile {
        font-size: 16px;
    }
}

.header__action-profile img {
    width: 20px;
    height: 25px;
    margin-right: 10px;
}

.header__action-logout {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-right: 0px;
    font-size: 14px;
    font-weight: 400;
    color: #000000;
}

@media (min-width: 460px) {
    .header__action-logout {
        font-size: 16px;
    }
}

.header__action-logout img {
    width: 20px;
    height: 25px;
    margin-right: 10px;
}
.product__register {
    max-width: 350px;
    width: 100%;
    margin: 0px auto;
}
.product__login {
    max-width: 350px;
    width: 100%;
    margin: 0px auto;
}
.products__item {
    width: calc(100% - 40px);
    min-height: 400px;
    display: flex;
    justify-content: center;
    margin: 0px 20px 20px;
}

@media (min-width: 460px) {
    .products__item {
        width: calc(50% - 40px);
        min-height: 475px;
        margin: 0px 20px 40px;
    }
}

@media (min-width: 768px) {
    .products__item {
        width: calc(33.333% - 40px);
        min-height: 500px;
    }
}

@media (min-width: 1024px) {
    .products__item {
        width: calc(25% - 40px);
        min-height: 550px;
    }
}

.products__content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.products__image {
    width: 100%;
    height: 350px;
}

@media (min-width: 768px) {
    .products__image {
        height: 350px;
    }
}

@media (min-width: 1024px) {
    .products__image {
        height: 400px;
    }
}

.products__image img {
    width: 100%;
    height: 100%;
}

.products__title {
    margin: 20px 0px 10px;
    text-align: center;
    font-size: 18px;
    font-weight: 700;
}

@media (min-width: 768px) {
    .products__title {
        font-size: 24px;
    }
}

.products__price {
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 500;
}

@media (min-width: 768px) {
    .products__price {
        font-size: 20px;
    }
}

.products__btn {
    padding: 10px 35px;
    margin-top: auto;
    color: #ffffff;
    font-size: 12px;
    font-weight: 700;
    border: 3px solid #000000;
    border-radius: 4px;
    text-transform: uppercase;
    background: #000000;
}

.products__btn:hover {
    border: 3px solid #000000;
    color: #000000;
    background: #ffffff;
}

@media (min-width: 768px) {
    .products__btn {
        font-size: 14px;
    }
}

.products__warning {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
